@import "partials/topbar";
@import "partials/header";
@import "partials/nav";
@import "partials/welcome";
@import "partials/news";
@import "partials/footer";
body {
    font-family: 'Ubuntu', sans-serif;
    color: #3c3b37;
    font-size: 12px;
    text-shadow: 1px 1px 1px #000;
    background: #0e100e url(../img/common/background.png) no-repeat center top;
}

.container {
    width: 990px !important;
    padding: 0 10px 0 10px;
}

h1 {
    color: #7f7861;
    font-size: 16px;
    text-shadow: 1px 1px 1px #000;
    font-weight: bold;
    margin: 10px 10px 5px 10px;
    padding: 0;
}

p {
    color: #3c3b37;
    font-size: 12px;
    text-shadow: 1px 1px 1px #000;
    font-weight: bold;
    margin-left: 10px;
    margin-right: 10px;
    padding: 0;
}

.news-item,
.left,
.right,
.middle {
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
    border-radius: 2px;
}

a:active,
a:focus {
    color: #af5959;
}