.welcome {
  padding-left: 21px;
  margin-top: 3px;
  overflow: hidden;

  .left, .right, .middle {
    height: 135px;
    float: left;
    margin-left: 3px;
  }

  .left, .right {
    width: 155px;
  }

  .middle {
    width: 619px;
    background: url(../img/common/welcome-middle.png) no-repeat center top;

    h1 {
      text-transform: uppercase;
    }
  }

  .left {
    margin-left: 0;
    background: url(../img/common/welcome-left.png) no-repeat center top;
  }

  .right {
    background: url(../img/common/welcome-right.png) no-repeat center top;
  }
}

.buttons {
  padding-left: 21px;
  margin-top: 5px;
  overflow: hidden;

  .left, .right, .middle {
    height: 40px;
    float: left;
    margin-left: 3px;
    line-height: 40px !important;
  }

  .left, .right {
    width: 155px;
    color: #806150;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: bold;

    &:hover {
      text-decoration: none;
    }
  }

  .middle {
    width: 619px;
    background: url(../img/common/news-header.png) no-repeat center top;

    h1 {
      text-transform: uppercase;
      font-size: 12px;
      padding-top: 0px;
      margin-top: 0px;
      line-height: 40px;
      margin-left: 17px;
    }

    .archive {
      display: block;
      float: right;
      font-weight: bold;
      font-size: 12px;
      text-transform: uppercase;
      margin-top: -45px;
      margin-right: 20px;

      .arrow {
        color: #b39d69;
        margin-right: 3px;
      }

      a {
        color: #8d8777;
      }
    }
  }

  .left {
    margin-left: 0;
    background: url(../img/common/button-side-left.png) no-repeat center top;
    padding-left: 67px;
  }

  .right {
    background: url(../img/common/button-side-right.png) no-repeat center top;
    padding-left: 17px;
  }
}
