nav {
  padding-left: 5px;

  .main-nav {
    width: 945px;
    height: 85px;
    margin: 0 auto;

    background: url(../img/common/nav-background.png) no-repeat center top;
    position: relative;

    .play-now {
      width: 203px;
      height: 72px;

      background: url(../img/common/play-now-button.png) no-repeat center top;
      position: absolute;
      left: 50%;
      margin-left: -101px;
      cursor: pointer;
      transition: all .2s ease-in-out;

      &:hover {
        background: url(../img/common/play-now-button-hover.png) no-repeat center top;
      }
    }

    ul {
      width: 360px;
      height: 80px;
      padding: 0;
      margin: 0;
      list-style-type: none;

      li, a {
        display: inline-block;
        width: 115px;
        line-height: 80px;
        height: 80px;
      }

      a {
        text-align: center;
        font-weight: bold;
        font-size: 12px;
        color: #84827d;
        text-shadow: 1px 1px 1px #000;
        text-transform: uppercase;
        transition: all .2s ease-in-out;

        &:hover {
          text-decoration: none;
          color: #af5959;
        }
      }

      &.left {
        float: left;
        margin-left: 5px;
      }

      &.right {
        float: right;
        margin-left: 5px;
      }
    }
  }
}
