footer {
  width: 628px;
  height: 83px;
  margin: 0 auto;
  background: url(../img/common/footer-bg.png) no-repeat right top;

  margin-bottom: 350px;
  padding-left: 45px;

  color: #4b4743;
  font-weight: bold;
  font-size: 12px;

  a {
    color: #79726b;

    &:hover {
      color: #af5959;
    }
  }

  .copyright, .links, .logo {
    width: 190px;
    height: 80px;
    float: left;
  }

  .logo {
    width: 180px !important;
  }

  .copyright {
    padding-top: 15px;

    .zafire, .peurr, .copyright-text {
      display: block;
    }
  }

  .links {
    padding-top: 25px;

    ul {
      list-style-type: none;
      margin: 0;
      padding: 0;

      li {
        display: inline-block;
        padding-left: 10px;
        padding-right: 10px;
      }
    }
  }
}
