.topbar {
  width: 100%;
  height: 42px;

  font-size: 12px;
  font-weight: bolder;
  color: #6c675b;
  text-shadow: 1px 1px 1px #000;

  background: url(../img/common/topbar-overlay.png) no-repeat center top;

  .col-md-6 {
    padding-right: 0px;
    padding-left: 10px;
  }

  .notice {
    line-height: 42px;

    span {
      color: #b36d6d;
    }

    .arrow {
      color: #b39d69;
      margin-right: 3px;
    }

    a {
      color: #8d8777;
    }
  }

  .login {
    line-height: 40px;

    input[type="text"], input[type="password"] {
      width: 185px;
      height: 30px;
      margin-right: 5px;

      font-size: 12px;
      line-height: 30px;
      color: #3c3c3c;
      text-shadow: 1px 1px 1px #000;

      background: url(../img/common/input-background.png) no-repeat;
      border: none;

      &::-webkit-input-placeholder { /* WebKit, Blink, Edge */
        color: #3c3c3c !important;
        text-shadow: 1px 1px 1px #000;
      }
      &:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
         color: #3c3c3c !important;
         text-shadow: 1px 1px 1px #000;
         opacity:  1;
      }
      &::-moz-placeholder { /* Mozilla Firefox 19+ */
         color: #3c3c3c !important;
         text-shadow: 1px 1px 1px #000;
         opacity:  1;
      }
      &:-ms-input-placeholder { /* Internet Explorer 10-11 */
         color: #3c3c3c !important;
         text-shadow: 1px 1px 1px #000;
      }

      &:active, &:focus {
        outline: none;
        box-shadow:0px 0px 0px 0px;
      }
    }

    input[type="submit"] {
      width: 70px;
      height: 23px;
      padding-top: 4px;
      padding-left: 10px;
      margin-top: 4px;

      color: #9a8e6d;
      font-weight: bold;
      font-size: 12px;
      text-shadow: 1px 1px 1px #000;

      background: url(../img/common/input-button-background.png) no-repeat;
      background-size: cover;
      background-color: rgba(0,0,0,0);
      border: 0;

      &:active, &:focus {
        outline: 0;
        border-box: 0px 0px 0px 0px;
      }
    }
  }
}
