.news {
  width: 619px;
  margin: 0 auto;
  height: 450px;
  padding-left: 8px;

  .news-item {
    height: 220px;
    background: url(../img/common/news-bg.png) no-repeat center top;
    margin-top: 4px;

    .header {
      width: 100%;
      display: block;
      color: #34322d;

      .divider, h1, .author, .date {
        display: inline-block;
      }

      .divider {
        margin: 0 2px;
      }

      h1 {
        color: #706545;
        font-size: 14px;
        padding-top: 10px;
        margin-right: 0px;
      }

      a {
        color: #7b3737;
      }

    }

    .content {
      a {
        color: #79726b;

        &:hover {
          color: #84827d;
        }
      }
    }

    .read-more-wrapper {
      margin-top: -6px;
      width: 100%;
      text-align: right;
      padding-right: 10px;

      .arrow, .read-more {
        display: inline-block;
        font-size: 12px;
        font-weight: bolder;
      }

      .arrow {
        margin-right: 3px;
        color: #b39d69;
      }

      .read-more {
        color: #6c675b;

        &:hover {
          cursor: pointer;
        }
      }
    }
  }
}
